/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
.block_switch_content {
	max-height: 500px;
	overflow-y: scroll;
	padding: 0 !important;
	border-radius: 10px !important;
}
.block_switch_content_container {
	background: white;
	overflow: hidden;
}
table {
	position: relative;
}
tr,
th,
td {
	background: white !important;
	outline: 0;
	border: none !important;
}
thead {
	background: white !important;
	outline: 0;
	border: none !important;
	position: relative;
	top: 0;
}
.block_switch_content_head_container {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	background: #0f91d6;
	border-radius: 5px;
	color: white !important;
	padding: 20px !important;
	cursor: pointer;
}
.block_switch_content_head {
	width: 100%;
	color: white !important;

	font-size: 20px;
	display: flex;
	align-items: center;
	justify-content: space-between !important;
}
.d-none {
	display: none !important;
}
#switch_hand {
	transition: all 0.3s;
	height: 0;
	overflow: hidden;
	&.active {
		height: 100%;
		overflow: visible;
	}
}
